import { mapState } from 'vuex';
export default {
	data() {
		return {
			input_data: '',
			up_sh_image: '',
			succeed_Br:true,
		};
	},
	computed: {
		...mapState(['userinfo'])
	},
	methods: {
		/* 提交表单 */
		async commit_form() {
			let base64 = this.base64;
			let input_data = this.input_data;
			input_data = input_data.replace(/\s+/g, '');
			if (input_data.length == 0 ) {
				let title = '';
				if (input_data.length == 0) {
					title = '请输入问题';
				}
				uni.showToast({
					title,
					icon: 'none',
					duration: 1000
				});
				
			} else {
				let userinfo = this.userinfo;
				let id = userinfo.id;
				if (id == undefined || id == null) {
					id = '';
				}
				let program_id = userinfo.program_id;
				let user_name = userinfo.name;
				if (user_name == undefined || user_name == null) {
					user_name = '';
				}
				let program_user_name = userinfo.nickName;
				let up_sh_image = this.up_sh_image;
				let res_Br = {};
				if (up_sh_image != '') {
					
					let { data: res } = await this.$http.upFile({
						url: '/topprogram/set-demand',
						filePath: up_sh_image,
						formData: {
							information: input_data + '',
							user_id: id + '',
							program_id: program_id + '',
							user_name: user_name + '',
							program_user_name: program_user_name + ''
						}
					});
					res_Br = JSON.parse(res);
				}else {
					 let { data: res } = await this.$http.post({
						 url:'/topprogram/set-demand',
						 contentType:"application/x-www-form-urlencoded",
						data:{
							information: input_data + '',
							user_id: id + '',
							program_id: program_id + '',
							user_name: user_name + '',
							program_user_name: program_user_name + ''
						}
					})
					
					res_Br = res
				}
				if (res_Br.status == 200) {
					this.succeed_Br = false;
					setTimeout(() => {
						uni.switchTab({
							url: '../My'
						});
					}, 2500);
				}
			}
		},
		/* 上传图片   和预览*/
		commit_image_data() {
			let str = this.up_sh_image;
			let that = this;

			if (str == '') {
				uni.chooseImage({
					count: 1,
					sourceType: ['album'],
					success: ({ tempFilePaths: path }) => {
						this.up_sh_image = path[0];
						uni.showToast({
							title: '上传成功，请提交',
							icon: 'none'
						});
					},
					fail: res => {
						uni.showToast({
							title: '上传失败',
							icon: 'none'
						});
					}
				});
			} else {
				uni.previewImage({
					current: 0,
					urls: [str]
				});
			}
		}
	}
};
